/* Main container */
.care-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px;
    background-color: #f9f9f9; /* Light background */
    flex-wrap: wrap; /* Ensure wrapping on smaller screens */
    width: 100%; /* Full width of the container */
    max-width: 1200px; /* Optional: Limit max width */
    margin: auto; /* Center alignment */
  }
  
  /* Left Images Section */
  .care-images-left {
    display: flex;
    flex-direction: column;
    gap: 20px; /* Space between images */
    position: relative;
  }
  
  .care-images-left .img1 {
    width: 150px;
    height: auto;
    border-radius: 10px;
  }
  
  .care-images-left .img2 {
    width: 150px;
    height: auto;
    border-radius: 10px;
  }


  /* Ensure the parent container is responsive */
.mx-auto {
  width: 100%; /* Full width of the container */
  max-width: 1200px; /* Optional: Limit max width */
  margin: auto; /* Center alignment */
}

/* Style for the image */
.responsive-img {
  width: 100%; /* Image will take full width of its container */
  height: auto; /* Maintain aspect ratio */
  object-fit: cover; /* Ensures the image fills the container while preserving aspect ratio */
 /* Optional styling for rounded corners */
}

  
  /* Center Text Section */
  .care-text {
    text-align: center;
    max-width: 300px; /* Limit text width */
  }
  
  .care-text h1 {
    font-size: 2.5rem;
  }
  
  .care-text p {
    margin-top: 10px;
  }
  
  .care-button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #0077b6; /* Blue button */
    color: white;
    border-radius: 10px;
  }
  
  /* Right Images Section */
  .care-images-right {
    display: flex;
    flex-direction: column;
    gap: 20px; /* Space between images */
  }
  
  .care-images-right .img3,
  .care-images-right .img4 {
    width: 150px;
    height: auto;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .care-section {
      flex-direction: column; /* Stack sections vertically on smaller screens */
      align-items: center; /* Center align all sections */
      gap: 30px; /* Add spacing between sections */
      padding: 20px; /* Reduce padding for smaller screens */
      text-align: center;
      overflow-x:hidden
  }
  }  


