.checkout-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
}

.delivery-info, .payment-methods {
  flex: 1 1 300px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  background-color: #fff;
}
.product-summary{
  margin: 50px;
  height: auto;
  border-radius: 8px;
  padding: 20px;

}

h2 {
  margin-bottom: 15px;
  font-weight: 700;
}

form .form-row {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
}

input[type="text"], input[type="email"], input[type="tel"] {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.product-item {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
  overflow: hidden;
}
.product-item img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 4px;
}

.product-details p {
  margin: 0;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

button {
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #f5f5f5;
  cursor: pointer;
}

.payment-options {
  display: flex;
  gap: 20px;
}




.product-summary {
  width: 100%;
  max-width: 400px;
  margin: auto;
  background: #ffffff; /* White background */
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  padding: 20px;
  color: #2d2d2d; /* Text color */
}

.product-summary h1 {
  font-size: 1.5rem;
  color: #1e2a5c; /* Matching color for the heading */
  margin-bottom: 20px;
}

.product-summary p {
  font-size: 1rem;
  margin: 10px 0;
}

.product-summary .total {
  font-size: 1.25rem;
  font-weight: bold;
  color: #1e2a5c; /* Total amount color */
}

.total {
 color: #1e2a5c; /* Total amount color */
}

input:checked + label {
  color: #1e2a5c !important; /* Optional: Add styling when input is selected */
}
.checkout-button {
  display: block;
  width: calc(100% - 5px);
  margin-top: 15px;
  padding: 10px;
  text-align: center;
  background-color: #1e2a5c; /* Button background color */
  color: #ffffff; /* White text */
  border-radius: 5px;
  border: none;
}

.checkout-button:hover {
  background-color: #16234a; /* Darker blue on hover */
}